import React, { useEffect, useState } from "react";
import styled, { keyframes } from "styled-components";
import { Link } from "react-router-dom";
// import Connection from "../";
import ChartInfo from "./Chart";
import Profile from "./Profile";
import InvestImg from "../../Resources/investing.svg";
import PortfolioImg from "../../Resources/portfolioimg.svg";
import StockImg from "../../Resources/stocksimg.svg";
import FinancialImg from "../../Resources/financialimg.svg";

export default function Homepage() {
  const [stock, setStock] = useState();
  const [articles, setArticles] = useState();
  const [article, setArticle] = useState();
  useEffect(() => {
    fetch(
      `https://newsapi.org/v2/top-headlines?category=business&page=2&language=en&apiKey=38801c44ca8e410290a82a2529881168`
    )
      .then((res) => res.json())
      .then((data) => setArticles(data.articles));
    fetch(
      "https://www.alphavantage.co/query?function=TIME_SERIES_WEEKLY&symbol=IBM&apikey=6V7SDPMNJBB6NGXM"
    )
      .then((res) => res.json())
      .then((data) => console.log("this is alpha data", data));
  }, []);
  if (articles) {
    console.log(articles);
  }

  if (articles) {
    return (
      <Wrapper>
        <Stocks>
          <Tag>Stocks </Tag>{" "}
          <Stock1 to="/stocks/AAPL/Apple%20Inc">
            <img src="data:image/png;base64," />
            <Bar></Bar>
            <CompanyInfo>Technology company</CompanyInfo>
            <SubCompanyInfo>since last week</SubCompanyInfo>{" "}
          </Stock1>
          <Stock2 to="/stocks/TSLA/Tesla%20Inc">
            <img src="data:image/png;base64," />
            <Bar></Bar>
            <CompanyInfo>Electric car company</CompanyInfo>
            <SubCompanyInfo>since last week</SubCompanyInfo>
          </Stock2>
          <Stock3 to="/stocks/NFLX/Netflix%20Inc">
            <img src="data:image/png;base64," />
            <Bar></Bar>
            <CompanyInfo>Production company</CompanyInfo>
            <SubCompanyInfo>since last week</SubCompanyInfo>
          </Stock3>
        </Stocks>
        <News>
          <Tag>News </Tag>
          <Category>
            <p>Business ˃</p>
            <p>Sports ˃</p>
            <p>Entertainment ˃</p>
            <p>Science ˃</p>
          </Category>
          <VertBar></VertBar>
          <Articles>
            <Trending>
              Trending
              <TrendingImg src={articles[0].urlToImage} />
              <h5>Author: {articles[0].author}</h5>
              <h6> {articles[0].title}</h6>
            </Trending>
            <Featured>
              <Article1>
                <Article1Img src={articles[1].urlToImage} />
                <h5>Author: {articles[1].author}</h5>
                <h6> {articles[1].title}</h6>
              </Article1>
              <Article2>
                <Article2Img src={articles[2].urlToImage} />
                <h5>Author: {articles[2].author}</h5>
                <h6> {articles[2].title}</h6>
              </Article2>
            </Featured>
          </Articles>
        </News>
        <BeginnerSection>
          <BeginnerHeader>New To Finance</BeginnerHeader>
          <BeginnerTags>
            <StockTag>
              Stocks
              <img src={StockImg} />
            </StockTag>
            <InvestmentTag>
              Investments
              <img src={InvestImg} />
            </InvestmentTag>
            <ProfileTag>
              Portfolio
              <img src={PortfolioImg} />
            </ProfileTag>
            <FinancialTag>
              Financial Statements
              <img src={FinancialImg} />
            </FinancialTag>
          </BeginnerTags>
        </BeginnerSection>
      </Wrapper>
    );
  } else {
    return (
      <Default>
        <Stocks>
          <p>Stocks :</p>
        </Stocks>
        <Stocks></Stocks>
      </Default>
    );
  }
}
const ProfileTag = styled.div`
  border: 2px solid #e9eaf0;
  background-color: white;
`;
const InvestmentTag = styled.div`
  border: 2px solid #e9eaf0;
  background-color: white;
`;
const FinancialTag = styled.div`
  border: 2px solid #e9eaf0;
  background-color: white;
`;
const StockTag = styled.div`
  border: 2px solid #e9eaf0;
  background-color: white;
`;
const BeginnerTags = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  height: 100%;
  gap: 20px 20px;
  background-color: #f5f6fa;
  border: 2px solid #e9eaf0;
  div:nth-child(n) {
    border-radius: 5px;
    max-width: 100%;
    max-height: 100%;
    padding: 10px;
    color: #737475;
    display: flex;
    justify-content: space-between;
  }
  :nth-child(n) {
    padding: 20px;
    border-radius: 5px;
  }
  img:nth-child(n) {
    width: 50%;
  }
`;

const BeginnerHeader = styled.div`
  width: 50%;
  font-size: 35px;
  color: #737475;
`;

const BeginnerSection = styled.div`
  display: flex;
  flex-direction: column;
  width: 95%;
  min-height: 750px;
  border-radius: 5px;
  border: 2px solid #e9eaf0;
  background-color: white;
  padding: 2.5%;
  margin: 2.5% 0;
  font-size: 25px;
`;

const TrendingImg = styled.img`
  width: 104%;
  height: 55%;
  margin: 20px 0;
  margin-left: -2%;
`;
const Article1Img = styled.img`
  height: 80%;
  width: 100%;
`;

const Article2Img = styled.img`
  height: 80%;
  width: 100%;
`;
const VertBar = styled.div`
  border: 1px solid #e9eaf0;
  margin-right: 15px;
  height: 105%;
`;
const Article2 = styled.div`
  display: flex;
  flex-direction: column;
  /* padding: 10px; */
  width: 95%;
  height: 47.5%;
  background-color: white;
  border-radius: 5px;
  /* border: 2px solid #e9eaf0; */
  :nth-child(n) {
    color: #737475;
  }
`;
const Article1 = styled.div`
  /* padding: 10px; */
  display: flex;
  flex-direction: column;
  width: 95%;
  height: 47.5%;
  border-radius: 5px;
  /* border: 2px solid #e9eaf0; */
  background-color: white;
  :nth-child(n) {
    color: #737475;
  }
`;
const Featured = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  width: 50%;
  height: 100%;
`;
const Trending = styled.div`
  display: flex;
  flex-direction: column;
  h6:nth-child(n) {
    margin: 20px 0;
    font-size: 15px;
  }
  /* justify-content: space-between; */
  padding: 10px;
  flex-direction: column;
  border: 2px solid #e9eaf0;
  color: #737475;
  border-radius: 5px;
  /* margin: 0px 5px; */
  height: 100%;
  width: 50%;
  background-color: white;
`;

const Articles = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
`;

const Category = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-evenly;
  padding-left: 2%;
  padding-right: 2%;
  :nth-child(2) {
    color: #737475;
    font-size: 20px;
  }
  width: 25%;
  height: 100%;
`;

const News = styled.div`
  display: flex;
  align-items: center;
  width: 95%;
  min-height: 750px;
  border-radius: 5px;
  border: 2px solid #e9eaf0;
  background-color: white;
  padding: 2.5%;
  margin: 2.5% 0;
  font-size: 25px;
`;

const SubCompanyInfo = styled.p`
  color: #a7a8ab;
  font-size: 17px;
`;

const CompanyInfo = styled.p`
  font-size: 20px;
  text-align: center;
  padding-bottom: 20px;
  color: #737475;
`;

const Tag = styled.div`
  height: 100%;
  color: #737475;
  font-size: 35px;
`;
const Stock3 = styled(Link)`
  display: flex;
  height: 75%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #e9eaf0;
  width: 25%;
`;
const Stock2 = styled(Link)`
  display: flex;
  height: 75%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #e9eaf0;
  width: 25%;
`;
const Stock1 = styled(Link)`
  display: flex;
  height: 75%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #e9eaf0;
  width: 25%;
`;

const Default = styled.div``;

const Stocks = styled.div`
  display: flex;
  overflow: auto;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  min-height: 750px;
  border-radius: 5px;
  font-size: 25px;
  border: 2px solid #e9eaf0;
  background-color: white;
  padding: 2.5%;
  padding-right: 5%;
  margin: 2.5% 0;
`;

const Bar = styled.div`
  border: 1px solid #e9eaf0;
  width: 90%;
  margin: 50px 0 25px 0;
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  background-color: transparent;
  align-items: center;
  overflow: auto;
  padding: 1% 2.5%;
  height: 100%;
`;
